header{
  width: 100%;
}

.covid-19-question
{
  width: 100%;
  background-color: #ce9f38;
  padding: 4px 10px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content:center;
  font-family: 'Mulish';
  font-weight: bold;
}

.covid-text{
  margin: auto;
  font-size: 14px;
}

.close-btn {
  background: none;
  color: #fff;
  border: none;
  font-size: 14px;
  cursor: pointer;
}
.logo-social-nav-container
{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000;
  padding:30px;
  color:#ce9f38;
}
.logo-container{
  flex:1
}
nav{
  flex: 1;
  display: flex;
  justify-content:center;
  align-items:center
}
.social-links-containers
{
  flex:1;
  display: flex;
  justify-content:flex-end
}

nav > ul{
  list-style: none;
  margin: 0;
  display:flex;
}

nav > ul > li{
  margin: 15px;
}

nav > ul > li > a{
font-family: 'Mulish';
text-decoration: none;
color:#ce9f38;
}

nav > ul > li > a:hover{
  font-weight: bold;
}