.services-section
{
  display: flex;
  padding: 100px;
  flex-direction: column;
}

.services-header
{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.section-title{
  flex: 1;
  text-align: center;
  font-family: 'Didot-bold';
  font-size: 24px;
  color: #040505;
}
.divider{
  flex: 3;
  height: 2px;
  border-radius: 10px;
  background-color: rgba(56, 56, 54, 0.12);
}
.services-container{
  padding: 80px 0px;
  display: flex;
  justify-content: space-between;
}
.service-item{
  flex:1;
  display: flex;
  flex-direction:column;
  justify-content: center;
  align-items: center;
  font-family: 'CharlevoixPro-Regular';
}
.service-item > img{
  width:170px;
  height:170px;
  margin-bottom: 20px;
}
.service-item > span{
  -webkit-text-stroke: 0.2px #1d1d1b;
  color: #383836;
} 