@font-face {
  font-family: "Mulish";
  src: url("./assets/fonts/Mulish/Mulish.ttf");
}
@font-face {
  font-family: "CharlevoixPro-Regular";
  src: url("./assets/fonts/CharlevoixPro-Regular.otf");
}
@font-face {
  font-family: "CharlevoixPro-Thin";
  src: url("./assets/fonts/CharlevoixPro-Thin.otf");
}
@font-face {
  font-family: "Didot-bold";
  src: url("./assets/fonts/Didot-Bold.ttf");
}

*{
  box-sizing: border-box;
}

body,html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  width: 100%;
  height: 100%;
}
