.covid-section
{
  display: flex;
  padding: 30px 100px;
  flex-direction: column;
  background-color: #040505;
  color: #ce9f38;
}
.header{
  -webkit-text-stroke: 0.5px #ce9f38;
  font-size: 22px;
  font-family: "CharlevoixPro-Regular";
}
.text{
  -webkit-text-stroke: 0.1px #ce9f38;
  font-size: 16px;
  font-family: "CharlevoixPro-Regular";
  line-height: 1.5;
}