.partners-section
{
  display: flex;
  padding: 100px;
  flex-direction: column;
}
.instagram-container{
  margin-top: 100px;
  display: flex;
}
.instagram-images-grid,
.instagram-images-text{
  flex: 1;
  margin:20px;
}
.instagram-images-grid{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
}
.instagram-image{
  width:100%;
  height:250px;
}
.instagram-image > img
{
  width:100%;
  height:100%;
  
}
.instagram-images-text{
  display: flex;
  flex-direction:column;
  justify-content: center;
}
.instagram-images-text > .text {
  font-family: 'Mulish';
  font-size: 30px;
  font-weight: bold;
  line-height: 1.33;
  letter-spacing: 0.3px;
  color: #040505;
}
.instagram-images-text > .description{
  -webkit-text-stroke: 0.3px #000000;
  font-family: 'CharlevoixPro-Thin';
  font-size: 16px;
  font-weight: 100;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.21;
  letter-spacing: 0.14px;
  text-align: left;
  color: #383836;
  margin-bottom: 40px;
}

.instagram-images-text > .hash-tag
{
  font-family: 'Mulish';
  font-size: 26px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.21;
  letter-spacing: 0.26px;
  text-align: left;
  color: #ce9f38;
}