.about-section
{
  display: flex;
  padding: 100px;
}
.about-text-container{

  padding: 100px 0px;
  flex: 1;
  font-family: 'CharlevoixPro-Regular'

}
.about-header
{
  font-size: 30px;
  color:#040505;
  -webkit-text-stroke: 0.5px #000000;
}

.about-text{
  color: #383836;
  line-height: 1.6;
  letter-spacing: 0.18px;
  font-size: 18px;

}
.about-images
{
  margin-left: 10px;
  flex: 1.4;
  justify-content: center;
  position:relative;
  display: flex;
}
.about-images > .front-image,
.about-images > .back-image
{
  height: 676px;


}
.about-images > .front-image
{
  width: 45%;
  position: absolute;
  transform: translate(-35%,10%);
}
.about-images > .front-image > img{
  height: 100%;
  width: 100%;
}
.about-images > .back-image
{
  width: 55%;
  background-color: #000;
}